@import "vars";

.tech-con {
    width: 60%;
    display: flex;
    flex-direction: column; }

%scores {
    margin-bottom: 1rem;
    border-bottom: 0.5px solid black;
    display: flex;
    justify-content: space-between;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-bottom: 1rem;
    font-size: 2.5rem;
    @include SecondFont; }

.scores {
    @extend %scores; }

.scores-small {
    font-size: 1.5rem;
    @extend %scores;

    &:first-child {
        margin-top: 1rem; } }
